.navbar li button.active {
    color: rgb(123, 255, 0); /* Change the text color to blue for the active section */
  }

  
.navbar {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    width: 90%;
    background-color: #696c70;
    padding: 5px;
    margin-left: 4%;
    margin-top: 1%;
    border-radius: 50px;
    color: white;
    grid-template-columns: repeat(2, minmax(200px, 1fr));
    box-shadow: 0px 2px 5px black;

  }
  
  .nav-text1{
    display: flex;
    width: 20%;
    height: 45px;
    justify-content: center;
    align-items: center;
  }

  ul{
    justify-content: center;
    align-items: center;
    display: flex;
    border: 2px solid rgb(72, 255, 0);
    width: 100%;
    list-style: none;
  }

  ul li{
margin: 0 1rem;
  }

  .btn-group{
    display: flex;
width: 90%;
height: fit-content;
justify-content: center;
align-items: center;
  }


.mobile-menu{
  display: none;
}

@media screen and (max-width: 999px) {
  .navbar {
    display: flex;
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: fit-content;
    background-color: #696c70;
    padding: 5px;
    margin-left: 0%;
    margin-top: 0%;
    border-radius: 0;
    color: white;
    box-shadow: 0px 2px 5px black;
  }

  .nav-text1{
    display: flex;
    width: 50%;
    height: 45px;
    justify-content: left;
    align-items: left;
    margin-left: 5%;
    
  }

  .btn-group{
    display: flex;
    height: auto;
    width: 100%;
    flex-direction: column;
    position: absolute;
    top: 40px;
    transition: 0.5s ease;
    left: -100%;
  }

  .btn-group.active{
    background-color: #696c70;
    opacity: 0.95;
    display: flex;
    height: 200px;
    width: 100%;
    flex-direction: column;
    position: absolute;
    top: 50px;
    transition: 0.5s ease;
    left: 0%;
   

  }
  .mobile-menu{
    display: block;
    font-size: 2rem;
    width: 50%;
    height: 45px;
    text-align: right;
    margin-right: 5%;
  }
  
}